import React, { useContext } from 'react';
import { Link } from 'react-router-dom';
import * as S from './Style';
import Logo from '../Logo/Logo';
import { ThemeContext, ThemeContextProps } from '../../Container/Home/Home';
//   ThemeContextProps(type )   . => import  

const Header = () => {
  //  : App.tsx  
  const { toggleTheme, isLight } = useContext(ThemeContext);
  const isLoggedIn = true;

  return (
    <S.Header>
      <div className="left">
        <Logo isAdmin={false} />
        <div className="darkmodew">
          {/* theme button: dark mode(default) */}
          <button type="button" onClick={toggleTheme} className={isLight ? 'dark' : 'light'}>
            <span>{/** /   */}</span>
          </button>
        </div>
      </div>
      <div className="right">
        {isLoggedIn ? (
          <div className="member">
            <Link to="/myaccount"></Link> <button type="button"></button>
          </div>
        ) : (
          <div className="guest">
            <Link to="/login"></Link>
            <Link to="/signup"></Link>
          </div>
        )}
      </div>
    </S.Header>
  );
};

export default Header;
