import styled from 'styled-components';

export const Chat = styled.div`
  position: absolute;
  bottom: 60px;
  right: 0;
  width: 50vw;
  max-width: 350px;
  border: 1px solid ${(props) => props.theme.borderColor};

  h3 {
  
    position: relative;
  
    //   
    background-color: ${(props) => props.theme.bgPanel};
    border-bottom: 1px solid ${(props) => props.theme.borderColor};
    padding: 0 20px;
    height: 50px;
    line-height: 50px;

    span {
      //  
      color: ${(props) => props.theme.textPale};
      font-weight: 300;
      padding-left: 4px;
    }
  }
  
  input {
      position: absolute;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
      height: 30px;
      border: 1px solid ${(props) => props.theme.borderColor};
      background-color: ${(props) => props.theme.bgColor};
      color: ${(props) => props.theme.textColor};
      border-radius: 4px;
      padding-left: 8px;
    }
  }

  .messagew {
    //   
    height: 40vh;
    max-height: 370px;
    background-color: ${(props) => props.theme.bgColor};
    padding: 14px 20px 6px;
    overflow-y: scroll;

    //  css
    &::-webkit-scrollbar {
      width: 5px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: ${(props) => props.theme.bgColor};
      border-radius: 5px;
    }

    &:hover::-webkit-scrollbar-thumb {
      background-color: ${(props) => props.theme.bgPanel};
      border-radius: 5px;
    }

    &::-webkit-scrollbar-track {
      background-color: ${(props) => props.theme.bgColor};
    }

    li {
      //  
      line-height: 1.6;
      margin-bottom: 8px;
      word-break: break-all;

      &.enter {
        // ,  
        text-align: center;
        font-size: 0.8125rem;
        color: ${(props) => props.theme.textPale};
        padding: 2px 0;
      }

      strong {
        //  
        font-weight: 500;
      }
    }
  }

  .writewrapper {
    //     
    height: 130px;

    .tab {
      height: 30px;
      line-height: 30px;
      text-align: right;
      display: flex;
      justify-content: flex-end;

      button {
        //   
        path,
        line {
          stroke: ${(props) => props.theme.textColor};
        }
      }
    }

    .textareaw {
      //   
      height: calc(100% - 30px);

      textarea {
        width: 100%;
        height: 100%;
        resize: none;
        padding: 10px 16px;
        border: 0;
      }
    }
  }
`;
