import React from 'react';
import * as S from './Style';
import Logo from '../../Components/Logo/Logo';
import { useForm } from 'react-hook-form';
import Instance from '../../Utils/api/axiosInstance';
import axios from 'axios';

const SignUp = () => {
  const {
    register,
    handleSubmit,
    formState: { isSubmitting },
  } = useForm();

  const onSubmit = (data: any) => {
    axios.post('/ideApi/api/member/signup', data, { headers: { 'Content-Type': 'application/json' } })
      .then(() => {
        alert(' .');
        window.location.href = '/login';
      })
      .catch((error) => console.log(error.data));
  };

  return (
    <S.Member>
      <div className="w">
        <Logo isAdmin={false} />
        <div>
          <form onSubmit={handleSubmit(onSubmit)}>
            <input {...register('memberId')} type="text" name="memberId" placeholder="" required />
            <input {...register('memberName')} type="text" name="memberName" placeholder="" required />
            <input {...register('password')} type="password" name="password" placeholder="" required />
            <input {...register('passwordCheck')} type="password" name="passwordCheck" placeholder=" " required />
            <input {...register('email')} type="text" name="email" placeholder="" required />
            <button type="submit" disabled={isSubmitting}>
              
            </button>
          </form>
        </div>
      </div>
    </S.Member>
  );
};

export default SignUp;
