import React from 'react';
import { Link } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import * as S from './Style';
import Logo from '../../Components/Logo/Logo';
import Instance from '../../Utils/api/axiosInstance';
import axios from 'axios';

const Login = () => {
  const {
    register,
    handleSubmit,
    formState: { isSubmitting },
  } = useForm();

  const onSubmit = (data: any) => {
    axios.post('/ideApi/api/member/login', data, { headers: { 'Content-Type': 'multipart/form-data' } })
      .then((response) => {
        console.log(response.data);
        if (response.status === 200) {
          window.location.href = '/';
        }
      })
      .catch((error) => console.log(error.data));
  };

  return (
    <S.Login>
      <div className="w">
        <Logo isAdmin={false} /> {/*      */}
        <div>
          <form onSubmit={handleSubmit(onSubmit)}>
            <input {...register('memberId')} type="text" name="memberId" placeholder="" required />
            <input {...register('password')} type="password" name="password" placeholder="" required />
            <button type="submit" disabled={isSubmitting}>
              
            </button>
          </form>
          <p className="link">
             ? <Link to="/signup"></Link>
          </p>
          <p className="link">
            / ? <Link to="/find">/ </Link>
          </p>
        </div>
      </div>
    </S.Login>
  );
};

export default Login;
