import React, { useState } from 'react';
import * as S from './Style';
import Logo from '../../Components/Logo/Logo';
import { useForm } from 'react-hook-form';
import Instance from '../../Utils/api/axiosInstance';
import { useLocation, useNavigate } from 'react-router-dom';

const ChangePassword = () => {
  const location = useLocation();
  const memberId = location.state?.memberId || '';
  const navigate = useNavigate();

  const passwordCheck = () => {
    return password !== newPassword && <p className="check">   .</p>;
  };

  const {
    register,
    handleSubmit,
    formState: { isSubmitting },
    watch,
  } = useForm();

  const password = watch('password');
  const newPassword = watch('newPassword');

  const changePassword = (data: any) => {
    if (password !== newPassword) {
      alert('   .');
      return;
    }

    const formData = {
      memberId: memberId,
      newPassword: data.newPassword,
    };

    Instance.post('/ideApi/api/member/changePassword', formData, {
      headers: { 'Content-Type': 'application/json' },
    })
      .then((response) => {
        if (response.data) {
          alert(' .  .');
          navigate('/login');
        }
      })
      .catch((e) => {
        console.error(e);
      });
  };

  return (
    <S.Find>
      <div className="w">
        <Logo isAdmin={false} />
        <div>
          <div>
            <p>
               .
              <br />
                 .
            </p>
            <form onSubmit={handleSubmit(changePassword)}>
              <input {...register('memberId')} type="hidden" name="memberId" placeholder="" required value={memberId} disabled />
              <input {...register('password')} type="password" name="password" placeholder="" required />
              <input {...register('newPassword')} type="password" name="newPassword" placeholder=" " required />
              {passwordCheck()}
              <button type="submit" disabled={isSubmitting || password !== newPassword}>
                 
              </button>
            </form>
          </div>
        </div>
      </div>
    </S.Find>
  );
};

export default ChangePassword;
